main {
    position: relative;
}
.site-footer {
    padding: 10px 0px 10px 0px;
}

.footer-link-items,
.footer-svg-link-items,
.center-content {
    text-align: center;
}

.four-oh-four {
    h1 {
        font-size: 3rem;
    }
}

.share-icon,
.donation-svg-link-items,
.footer-svg-link-items {
    a {
        padding: 0em .6em 0em .6em;
    }
    svg {
        width: 35px;
        height: 35px;
        stroke: $main-important;
    }
    svg:hover {
        cursor: pointer;
        stroke: $background-color;
        fill: $main-important;
    }
}

.footer-link-items{
    a {
        color: $text-color;
        padding: 0px 10px 0px 10px;
    }
}

.share-post-icons {
    margin-top: 10px;
    svg {
        width: 30px;
        height: 30px;
    }
    a {
        margin-right: 10px;
    }
}

.footer-link-items,
.footer-svg-link-items {
    a:first-child {
        padding-left: 0;
    }
    a:last-child {
        padding-right: 0;
    }
}

.strong {
    color: $main-important !important;
}

.home {
    h1 {
        font-size: 1.6em;
    }
}

.citation {
    line-height: 1em;
    margin-bottom: 15px;
}

.hidden {
    display: none;
}

#cookie-banner {
    position: sticky;
    top: 0;
    display: none;
    width: 100%;
    text-align: center;
    color: $text-color;
    background-color: $background-color;
    border-bottom: 5px solid $brand-color;
    z-index: 1337;
    margin-bottom: -5px;
    .cookie-container {
        padding: 10px 20px 10px 20px;
    }
    a {
        cursor: pointer;
    }
    .consent-button {
        padding: .1rem .4rem;
        margin: .4rem;
    }
}

.btn {
    cursor: pointer;
    display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    user-select: none;
    border: 1px solid;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;

    background-color: transparent;
    transition: color .15s ease-in-out,
        background-color .15s ease-in-out,
        border-color .15s ease-in-out,
        box-shadow .15s ease-in-out;
}
.btn:hover {
    color: $text-color;
}

.btn-outline-danger {
    color: $danger-red;
}
.btn-outline-danger:hover {
    border-color: $danger-red;
    background-color: $danger-red;
}
.btn-outline-success {
    color: $success-green;
}
.btn-outline-success:hover {
    border-color: $success-green;
    background-color: $success-green;
}

.float-right {
    float: right;
}

.post-image-wrapper {
    margin-bottom: 40px;
    img {
        max-width:100%;
        max-height:100%;
        margin-left: auto;
        margin-right: auto;
        display: block;
    }
}
